/* For phone screens */

@media only screen and (max-width: 980px) {
  .navbar-nav {
    width: 100%;
  }
  h1 {
    font-size: 2rem !important;
    margin-bottom: 0.5rem !important;
    display: block;
  }
  h2 {
    font-size: 1.5rem !important;
  }
  .btn-leos-blue-large {
    width: 150px !important;
    font-size: 1.25rem !important;
  }
  .btn-leos-blue {
    width: 125px !important;
    font-size: 1rem !important;
  }
  .header-image-large img {
    margin-top: 15vh !important;
  }
  .header-image-small img {
    margin-top: 15vh !important;
  }
  .arrow-down {
    margin-top: 35px;
  }
  .large-screen {
    display: none !important;
  }
  .acronym-leos {
    font-size: 80px !important;
    line-height: 1;
  }
  .acronym-word {
    height: 80px;
    text-align: left !important;
    padding-top: 45px;
  }
  .col-7 {
    padding-left: 0 !important;
  }
  .map-section {
    margin-top: 100px;
  }
  .amenities-tile {
    height: 175px !important;
  }
  #campground-map {
    width: 100%;
  }
}

/* For laptop screens */

@media only screen and (min-width: 980px) {
  .navbar-nav {
    width: 65%;
  }
  h1 {
    font-size: 4rem !important;
  }
  .btn-leos-blue-large {
    width: 200px !important;
    font-size: 1.5rem !important;
    margin-bottom: 10px !important;
  }
  .btn-leos-blue {
    width: 150px !important;
    font-size: 1.25rem !important;
  }
  .arrow-down {
    margin-top: 50px;
  }
  .small-screen {
    display: none !important;
  }
  .acronym-leos {
    font-size: 250px !important;
  }
  .blue-tile {
    background: #001830 !important;
  }
  .white-tile {
    background: white !important;
  }
  #tiki:hover {
    background-image: url(images/tiki-hut.png) !important;
  }
  #chess:hover {
    background-image: url(images/chess.png) !important;
  }
  #games:hover {
    background-image: url(images/games.png) !important;
  }
  #iguana:hover {
    background-image: url(images/iguana.png) !important;
  }
  #campground-map {
    width: 75%;
  }
}

html, body {
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 0px;
  overflow-x: hidden;
}

.navbar-dark .navbar-nav .nav-link {
  color: rgba(255, 255, 255, 0.75);
}

.home-icon {
  padding: 0 !important;
}

.home-icon img:hover {
  opacity: 0.8;
}

.white-background {
  background: white;
  height: 100vh;
  padding: 0 16px;
}

.main-image {
  background-image: linear-gradient(rgba(0, 0, 0, 0.25), rgba(0, 0, 0, 0.25)), url(images/background-image.png);
}

.mission-image {
  background-image: linear-gradient(rgba(0, 0, 0, 0.25), rgba(0, 0, 0, 0.25)), url(images/mission-background.png);
}

.background-image {
  height: 100vh;
  padding: 0 16px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.header-text {
  color: white;
}

.header-image-large img {
  height: 30vh;
  margin-top: 20vh;
  margin-bottom: 10px;
}

.header-image-small img {
  height: 30vh;
  margin-top: 20vh;
  margin-bottom: 10px;
}

.header-image-small img {
  height: 25vh;
  margin-top: 20vh;
  margin-bottom: 10px;
}

.margin-top {
  margin-top: 50px;
}

.main-section {
  height: 100vh;
  width: 100vw;
  padding: 75px 16px 25px 16px;
}

.value-item {
  margin: 0 10px;
}

.acronym-leos {
  text-align: center;
  font-weight: bold;
  color: #001830;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.acronym-word {
  text-align: center;
}

.mission-statement {
  text-align: center;
  line-height: 1.5;
  margin-top: 50px;
}

.amenities-tile {
  display: table;
  width: 100%;
  height: 250px;
  text-align: center;
}

.amenities-text {
  display: table-cell;
  vertical-align: middle;
}

.white {
  color: white;
}

.text-center {
  text-align: center;
}

.blue-tile {
  color: white;
}

.white-tile {
  color: #001830;
}

#tiki {
  background-image: url(images/tiki-hut.png);
}

#chess {
  background-image: url(images/chess.png);
}

#games {
  background-image: url(images/games.png);
}

#iguana {
  background-image: url(images/iguana.png);
}

/* ----------Animations---------- */

@keyframes slideInFromLeft {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0);
  }
}

@keyframes slideInFromTop {
  0% {
    transform: translateY(-100%);
  }
  100% {
    transform: translateX(0);
  }
}

#rv-icon {
  /* This section calls the slideInFromLeft animation we defined above */
  animation: 2.5s ease-out 0s 1 slideInFromLeft;
}